/* Quiz.css */
.quiz {
    max-width: 750px;
    margin: auto;
    padding: 20px;
    border-radius: 10px;
    background-color: #f9f9f9;
    border: 1px solid #8b4d4d;
    
  }
  
  .score-section {
    text-align: center;
  }
  
  .question-section {
    margin-bottom: 20px;
  }
  
  .question-count {
    font-size: 18px;
  }
  
  .question-text {
    font-size: 16px;
  }
  
  .answer-section button {
    display: block;
    width: 100%;
    padding: 10px;
    margin-bottom: 10px;
    border: 1px solid #8b4d4d;
    border-radius: 5px;
    background-color: #2ab8bb;
    cursor: pointer;
    transition: background-color 0.3s ease;
  }
  
  .answer-section button:hover {
    background-color: #f0f0f0;
  }
  .cer {
    background: #1877f2;
    border: 1px solid #1877f2;
    color: #fff;
    font-size: 1.25rem;
    padding: 0.5rem;
    margin: 0.5rem 0;
    border-radius: 8px;
    outline: none;
    cursor: pointer;
  }