.certificate {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width:700px;
    height: 500px;
    background-image: url('Certificate-template-illustration-on-transparent-background-PNG.png');
    background-size: cover;
    background-position: center;
    padding: 20px;

  }
  
  .certificate h1 {
    font-size: 35px;
    color: #333;
  }
  
  .certificate p {
    font-size: 18px;
    color: #666;
  }
  
  .certificate .score {
    font-size: 20px;
    font-weight: bold;
    color: #00bcd4;
  }
  